  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.nav__link, .dropdown__item, .nav__link--members {
  margin: 0 0.625rem;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  line-height: 1.33rem;
  font-weight: 600;
  color: #64748b;
  border-radius: 0.25rem;
  background-color: transparent;
  transition: background-color 0.3s ease-in-out;
}
.nav__link:hover, .dropdown__item:hover, .nav__link--members:hover {
  background-color: #f8fafc;
}

