  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #f1f5f9;
  border-radius: 0.25rem;
}
@media screen and (min-width: 64rem) {
  .nav__hamburger {
    display: none;
  }
}

.nav__logo--hamburger {
  width: auto;
  height: 1.5rem;
}

.nav__btn {
  width: unset;
  max-width: unset;
  min-height: 1.75rem;
  margin-left: 0.625rem;
}

.dropdown {
  position: relative;
  cursor: pointer;
}

.dropdown__menu {
  position: absolute;
  top: calc(100% + 1.25rem);
  right: 0;
  left: 0;
  margin: auto;
  width: max-content;
  padding: 1rem;
  border-radius: 0.75rem;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  background-color: #ffffff;
  border: 2px solid #f1f5f9;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -2px rgba(0, 0, 0, 0.1);
}

.dropdown__item {
  margin: unset;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  line-height: 1;
}

.dropdown__icon {
  display: inline-block;
  width: 1.25rem;
  height: 1.25rem;
  vertical-align: middle;
  margin-top: -0.25rem;
}

.hello__link {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
